/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/*---------------------------
## Elements
---------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: var(--color__background-body);
}

blockquote, q {
  quotes: "" "";
}
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
}

hr {
  background-color: var(--color__background-hr);
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */
}

figure {
  margin: 0;
  /* Extra wide images within figure tags don't overflow the content area. */
}

/* LINKS */
a {
  color: var(--color__link);
}
a:hover, a:focus, a:active {
  color: var(--color__link-hover);
}
a:focus {
  outline: thin dotted;
  outline-offset: 0.25rem;
}
a:hover, a:active {
  outline: 0;
}

/* LISTS */
ul, ol {
  margin: 0 0 0 0;
}

ul {
  list-style: disc;
  padding-left: 1rem;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 0;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 0 0 0;
}

/* TABLES */
table {
  margin: 0 0 0;
  width: 100%;
}

/*---------------------------
## Typography
---------------------------*/
html {
  font-size: var(--font__size-root-small);
  letter-spacing: var(--font__letter-spacing);
}
@media (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait) {
  html {
    font-size: 3vw;
  }
}
@media (min-width: 992px) {
  html {
    font-size: var(--font__size-root-medium);
  }
}
@media (min-width: 1200px) {
  html {
    font-size: var(--font__size-root-large);
  }
}

body,
button,
input,
select,
optgroup,
textarea {
  color: var(--color__text-main);
  font-family: var(--font__main);
  font-weight: 300;
  font-size: 1rem;
  line-height: var(--font__line-height-body);
}

/* HEADINGS */
.entry-title {
  font-size: 4.5rem;
  line-height: 1;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  clear: both;
  font-weight: bold;
  line-height: 1.1;
  margin: 0;
  letter-spacing: 0.05rem;
  font-family: var(--font__serif);
}

h1, .h1 {
  font-size: 2.1rem;
  line-height: 1.25;
}
@media (min-width: 992px) {
  h1, .h1 {
    font-size: 2.1rem;
  }
}

h2, .h2 {
  font-size: 2rem;
}
@media (min-width: 992px) {
  h2, .h2 {
    font-size: 2rem;
  }
}

h3, .h3 {
  font-size: 1.7rem;
}
@media (min-width: 992px) {
  h3, .h3 {
    font-size: 1.8rem;
  }
}

h4, .h4 {
  line-height: 1.5;
  font-size: 1.5rem;
}
@media (min-width: 992px) {
  h4, .h4 {
    font-size: 1.5rem;
  }
}

h5, .h5 {
  font-size: 1.25rem;
}

h6, .h6 {
  font-size: 1rem;
  font-weight: bold;
}

legend {
  line-height: 1.4;
}

/* COPY */
p {
  margin-top: 0;
  margin-bottom: 1em;
}
p:last-child {
  margin-bottom: 0;
}
p:empty {
  display: none;
}
p.first-letter::first-letter {
  color: white;
  float: left;
  font-size: 1.9rem;
  line-height: 0.9;
  margin-right: 0.2rem;
  font-family: var(--font__serif);
  margin-top: -0.29rem;
}

a.link {
  position: relative;
  letter-spacing: var(--font__letter-spacing--xl);
  text-decoration: none;
  font-size: 0.6rem;
  text-transform: uppercase;
}
a.link:after {
  content: "";
  position: absolute;
  bottom: -0.2rem;
  left: 0;
  opacity: 1;
  width: 100%;
  height: 1px;
  transform-origin: 50% 50%;
  transform: scaleX(1);
  background-color: currentColor;
  transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
}
a.link:hover:after {
  transform: scaleX(0.8);
}
a.link:before {
  /*
  content: '';
  position: absolute;
  left: -0.5rem;
  top: -1rem;
  height: calc(100% + 2rem);
  width: calc(100% + 1rem);
  */
}

dfn, cite, em, i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

abbr, acronym {
  border-bottom: 1px dotted var(--color__border-abbr);
  cursor: help;
}

mark, ins {
  background: var(--color__background-ins);
  text-decoration: none;
}

big {
  font-size: 125%;
}

/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
/*---------------------------
## Header
---------------------------*/
.site__brand {
  width: 6rem;
  position: fixed;
  z-index: 10000;
  line-height: 0;
  transition: all 0.4s ease-in-out;
  margin-top: 0rem;
  z-index: 999999;
  left: 50%;
  margin-left: -3rem;
  top: 1.1rem;
}
@media (min-width: 992px) {
  .site__brand {
    left: 50%;
    margin-left: -3.25rem;
    width: 6.5rem;
    top: 1rem;
    padding-top: 0;
  }
  @supports (mix-blend-mode: multiply) {
    .site__brand {
      mix-blend-mode: difference;
    }
    .site__brand svg path[fill="#000000"] {
      fill: white;
    }
  }
}
.site__brand .o-logo path {
  transition: all 0.4s ease-in-out;
}

.site__brand-sign {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;
  will-change: opacity, visibility;
}

.site__navigation-social svg {
  fill: var(--color__white);
}
.site__navigation-social svg path[fill="#FFFFFF"] {
  fill: var(--color__white);
}

@media (max-width: 992px) {
  body.is-navigation--open .site__brand-sign .o-logo path[fill="#FFFFFF"] {
    fill: white;
  }
  body.is-navigation--open .site__brand-sign .o-logo path[stroke="#FFFFFF"] {
    stroke: white;
  }
  body.is-navigation--open .site__navigation-social svg,
body.is-navigation--open .site__navigation-toggle svg {
    fill: white;
  }
  body.is-navigation--open .site__navigation-social svg path[fill="#FFFFFF"],
body.is-navigation--open .site__navigation-toggle svg path[fill="#FFFFFF"] {
    fill: white;
  }
  body.is-navigation--open .site__navigation-social svg circle[stroke="#FFFFFF"],
body.is-navigation--open .site__navigation-toggle svg circle[stroke="#FFFFFF"] {
    stroke: white;
  }
}

/*---------------------------
## Navigation
---------------------------*/
.site__navigation {
  --nav__icon-size--sm: 2rem;
  --nav__icon-size--lg: 2.25rem;
  --nav__toggle-size--sm: 2.75rem;
  --nav__contact-size--md: 13rem;
  --nav__contact-size--lg: 15rem;
}

.site__navigation-main,
.site__navigation-user {
  z-index: 999998;
}

.site__navigation-toggle {
  z-index: 999999;
}

.site__navigation-main {
  position: fixed;
  left: 0rem;
  top: 0rem;
  height: 100vh;
  width: 100vw;
  background-color: white;
  transform: translateX(-100%);
  transition: transform 0.4s cubic-bezier(0.55, 0, 0.1, 1);
  display: flex;
  align-items: center;
}
.site__navigation-main > ul.nav-menu {
  list-style: none;
  padding: 0;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.site__navigation-main > ul.nav-menu li {
  width: 100%;
  text-align: center;
}
.site__navigation-main .menu-item {
  font-size: 1.25rem;
}
.site__navigation-main .menu-item a {
  display: block;
  text-decoration: none;
  font-weight: bold;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
.site__navigation.is-open .site__navigation-main {
  transform: translateX(0%);
}
@media (min-width: 992px) {
  .site__navigation-main {
    background-color: transparent;
    width: auto;
    height: auto;
    left: 4rem;
    top: 2rem;
    transform: unset;
  }
  @supports (mix-blend-mode: multiply) {
    .site__navigation-main {
      mix-blend-mode: difference;
      color: white;
    }
  }
  .site__navigation-main > ul.nav-menu {
    display: flex;
    width: auto;
    flex-wrap: nowrap;
  }
  .site__navigation-main > ul.nav-menu li {
    width: auto;
    text-align: left;
  }
  .site__navigation-main > ul.nav-menu li + li {
    margin-left: 1rem;
  }
  .site__navigation-main .menu-item {
    font-size: 0.75rem;
  }
  .site__navigation-main .menu-item a {
    text-decoration: none;
    font-weight: bold;
    padding-top: 0rem;
    padding-bottom: 0rem;
    position: relative;
  }
  .site__navigation-main .menu-item a::after {
    content: "";
    transform: scaleX(0);
    position: absolute;
    transition: transform 0.15s ease-in-out;
    height: 2px;
    left: 0;
    bottom: -2px;
    width: 100%;
    background-color: currentColor;
  }
  .site__navigation-main .menu-item.current-menu-item a::after {
    transform: scaleX(1);
  }
}

.site__navigation-user {
  position: fixed;
  top: 1.6rem;
  right: 1.25rem;
}
@media (min-width: 992px) {
  .site__navigation-user {
    right: 4rem;
    top: 2rem;
  }
  @supports (mix-blend-mode: multiply) {
    .site__navigation-user {
      mix-blend-mode: difference;
      color: white;
    }
    .site__navigation-user svg [stroke=black],
.site__navigation-user svg [stroke="#000"] {
      stroke: #FFF;
    }
    .site__navigation-user svg path[fill-rule=evenodd] {
      fill: white;
    }
  }
}

.site__navigation-toggle {
  display: block;
  background-color: transparent !important;
  border: none;
  border-radius: 0 0 0 0;
  font-size: 1rem;
  color: transparent;
  padding: 0rem;
  overflow: hidden;
  position: fixed;
  left: 0.45rem;
  top: 1rem;
  display: flex !important;
  flex-wrap: wrap;
  align-items: center;
  width: var(--nav__toggle-size--sm);
  height: var(--nav__toggle-size--sm);
}
.site__navigation-toggle[aria-expanded=true] svg path[fill="#FFFFFF"] {
  fill: var(--color__white-full) !important;
}
.site__navigation-toggle[aria-expanded=true] svg circle[stroke="#FFFFFF"] {
  stroke: var(--color__white-full) !important;
}
@media (min-width: 992px) {
  .site__navigation-toggle {
    display: none !important;
  }
}
.site__navigation-toggle > svg {
  width: inherit;
  height: inherit;
}

.site__navigation-contact {
  width: var(--nav__contact-size--lg);
}

/**
 * Animations
 */
.site__navigation-main ul.nav-menu {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.05s 0.5s, visibility 0.05s 0.5s;
  margin-top: -1rem;
}
.site__navigation-main ul.nav-menu::before {
  transform: translateX(100%);
  transition: transform 0.5s 0.05s cubic-bezier(0.33, 1, 0.68, 1);
}
.site__navigation-main ul.nav-menu li {
  transform: translateY(50%);
  opacity: 0;
  visibility: hidden;
  transition: transform 0.4s cubic-bezier(0.33, 1, 0.68, 1), visibility 0.4s cubic-bezier(0.33, 1, 0.68, 1), opacity 0.4s cubic-bezier(0.33, 1, 0.68, 1);
}
.site__navigation-main ul.nav-menu li:nth-child(2), .site__navigation-main ul.nav-menu li:nth-child(3), .site__navigation-main ul.nav-menu li:nth-child(4), .site__navigation-main ul.nav-menu li:nth-child(5), .site__navigation-main ul.nav-menu li:nth-child(6), .site__navigation-main ul.nav-menu li:nth-child(7), .site__navigation-main ul.nav-menu li:nth-child(8) {
  transition-delay: 0s;
}
@media (min-width: 992px) {
  .site__navigation-main ul.nav-menu li {
    transform: none;
    opacity: 1;
    visibility: visible;
    transition: none;
  }
}
@media (min-width: 992px) {
  .site__navigation-main ul.nav-menu {
    margin-top: 0;
    transition: none;
    opacity: 1;
    visibility: visible;
  }
}

.site__navigation.is-open > .site__navigation-main ul.nav-menu {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.05s 0s, visibility 0.05s 0s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu::before {
  transform: translateX(0%);
  transition: transform 0.5s 0.05s cubic-bezier(0.33, 1, 0.68, 1);
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li {
  transform: translateY(0%);
  opacity: 1;
  visibility: visible;
  transition: transform 0.4s cubic-bezier(0.33, 1, 0.68, 1), visibility 0.4s cubic-bezier(0.33, 1, 0.68, 1), opacity 0.4s 0.6s cubic-bezier(0.33, 1, 0.68, 1);
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(1) {
  transition-delay: 0.25s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(2) {
  transition-delay: 0.35s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(3) {
  transition-delay: 0.45s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(4) {
  transition-delay: 0.55s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(5) {
  transition-delay: 0.65s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(6) {
  transition-delay: 0.75s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(7) {
  transition-delay: 0.85s;
}
.site__navigation.is-open > .site__navigation-main ul.nav-menu li:nth-child(8) {
  transition-delay: 0.95s;
}

.site__navigation.is-open > .site__navigation-social svg {
  fill: white !important;
}
.site__navigation.is-open > .site__navigation-social svg path {
  fill: white !important;
}

/*---------------------------
## Grid
---------------------------*/
.container {
  width: var(--size__site-main);
  max-width: 100%;
  display: block;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
@media (min-width: 754px) {
  .container {
    width: var(--size__site-small);
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 992px) {
  .container {
