}
.c-select::after, .c-select::before {
  content: "";
  width: 0.5rem;
  height: 0.3rem;
  background-color: black;
  clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  position: absolute;
  top: calc(50% - 0.4rem);
  right: 1rem;
  z-index: 1;
  transform-origin: 50% 50%;
}
.c-select::before {
  transform: rotate(180deg);
}
.c-select::after {
  top: calc(50% + 0.25rem);
}
.c-select .wpcf7-not-valid-tip {
