    bottom: 4rem;
    right: 4rem;
  }
}
.o-mouse--center {
  right: calc(50% - 0.75rem);
  margin-left: 0;
}
.o-mouse--left {
  right: calc(100% - 2rem);
}
@media (min-width: 992px) {
  .o-mouse--left {
    right: calc(100% - 4rem);
  }
}
